<template>
  <div>
    <pages-list></pages-list>
    <ul class="movies-container">
      <movie-box
        v-for="movie in movies"
        v-bind:key="movie.id"
        v-bind:title="movie.title"
        v-bind:id="movie.id"
        v-bind:views="movie.views"
        v-bind:is-translated-to-polish="Boolean(movie.isTranslatedToPolish)"
        v-bind:pornstars="extractPornstars(movie.pornstars)"
        v-bind:duration="movie.duration"
      ></movie-box>
    </ul>
    <pages-list></pages-list>
    <movie-preview-complete />
  </div>
</template>

<script lang="ts">
import MovieBox from "@jscomponents/movies/movie_box.vue";
import PagesList from "@jscomponents/pages_list.vue";
import { MovieBasicData } from "@interfaces/movies/MovieBasicData";
import { BasicPornstarData } from "@interfaces/pornstars/BasicPornstarData";
import PagesListBasicData from "@interfaces/pages_list_basic_data";
import MoviePreviewComplete from "@jscomponents/movies/movie_preview_complete.vue";
import PageListUpdate from '@interfaces/PageListUpdate';

export default {
  name : 'movies-list',

  props : {
    initialMoviesData : {
    type: Object,
    required: false,
    default: undefined,
  },

  initialTotalMovies : {
    type: Number,
    required: false,
    default: undefined,
  },

  moviesPerPage : {
    type: Number,
    required: false,
    default: 100,
  }
  },

  components : {
     MovieBox, 
     PagesList, 
     MoviePreviewComplete
  },

  data() {
    return {
      movies: [],
      totalMovies:  undefined,
    }
  },

  created() {
    if (this.initialMoviesData) {
      this.movies = this.initialMoviesData.movies;
    }

    if (this.initialMoviesData) {
      this.totalMovies = this.initialTotalMovies;
    }
  //@ts-ignore
    this.emitter.on("updateMoviesList", this.updateMoviesList);
    //@ts-ignore
    this.emitter.on('clearMoviesList', this.clearMoviesList)
  },

  methods : {
    clearMoviesList(){
    this.totalMovies = 0;
    this.movies = [];
    //@ts-ignore
    this.emitter.emit('clearPagesList');
  },

  updateMoviesList(moviesList: PageListUpdate<MovieBasicData>): void {
    this.totalMovies = moviesList.totalElements
    this.movies = moviesList.content
    const amountOfSubPages: number = Math.ceil(this.totalMovies / this.moviesPerPage);
    this.updatePagesList({
      pagesNumber: amountOfSubPages,
      currentPage: moviesList.currentPage,
    });
  },

  updatePagesList(data: PagesListBasicData) {
    //@ts-ignore
    this.emitter.emit("updatePagesList", data);
  },

  extractPornstars(pornstarsList: BasicPornstarData[]): string[] | string {
    if (pornstarsList && pornstarsList.length > 0) {
      let parsedPornstarslist: string[] = [];

      pornstarsList.forEach((pornstar) => {
        parsedPornstarslist.push(pornstar.nickname);
      });

      return parsedPornstarslist;
    } else {
      return "";
    }
  }
  }


}
</script>

<style lang="scss">
@import "~sasscomponent/movies/movies_container";
</style>
